import React from 'react'
import { DragDropContext, Draggable, Droppable } from 'react-beautiful-dnd'
import List from './List'

function Lists({ todos, setTodos }) {
  /**
   * FIXME: dnd memo   
   * Warning: Connect(Droppable): Support for defaultProps will be removed from memo components
   * in a future major release.
   * Use JavaScript default parameters instead.
   */
  const onDragEndHandler = (result) => {
    // result  source          
    console.log(result)

    //  ( )   .
    if (!result.destination) return

    //      todoData 
    const newTodos = [...todos]
    // const newTodos = Array.from(todos)
    // const newTodos = todos.slice()

    /**
     * 1.    .
     * 2. return    .
     */
    const [reOrderItem] = newTodos.splice(result.source.index, 1)

    //   reOrderItem .
    newTodos.splice(result.destination.index, 0, reOrderItem)
    setTodos(newTodos)
    localStorage.setItem('todos', JSON.stringify(newTodos))
  }

  return (
    <DragDropContext onDragEnd={onDragEndHandler}>
      <Droppable droppableId="todoList">
        {(provided) => (
          <ul {...provided.droppableProps} ref={provided.innerRef}>
            {todos.map((todo, index) => (
              <Draggable key={todo.id} draggableId={todo.id.toString()} index={index}>
                {/* eslint-disable-next-line no-shadow */}
                {(provided, snapshot) => (
                  <List
                    todos={todos}
                    setTodos={setTodos}
                    todo={todo}
                    provided={provided}
                    snapshot={snapshot}
                  />
                )}
              </Draggable>
            ))}
            {provided.placeholder}
          </ul>
        )}
      </Droppable>
    </DragDropContext>
  )
}

export default React.memo(Lists)
